import React from 'react';
import {Surface} from '../Surface';
import {PositionBlockProps} from './position-types';

export function PositionBlock({children, style = {}, classes = [], onClick, position, ...props}: PositionBlockProps) {
    return <Surface style={{...props, ...style, position}}
                    classes={classes}
                    onClick={onClick}>
        {children}
    </Surface>;
}
