import React from 'react';
import makeClassString from 'classnames';
import {useAnonymousStyle} from '../utilities/utilities';
import {SurfaceProps} from './surface-types';

export function Surface({children, style = {}, classes = [], ...props}: SurfaceProps) {
    const ref = useAnonymousStyle({...props, ...style});
    const className = makeClassString(classes, ref.current);
    const {onClick} = props;
    return <div onClickCapture={onClick} className={className}>
        {children}
    </div>;
}
