import {ActionProps, PositionProps} from '../types';

export type PositionDimensionProps = PositionProps & ActionProps;

export enum PositionTypes {
    Absolute = 'absolute',
    Fixed = 'fixed',
    Initial = 'initial',
    Relative = 'relative',
    Static = 'static',
    Sticky = 'sticky'
}

export interface PositionBlockProps extends PositionDimensionProps {
    position: PositionTypes;
}
